import React from 'react'
import Helmet from 'react-helmet'
import { Link } from '../_style'
import get from 'lodash/get'
import Header from '../components/main-wrapper'
// import Bio from '../components/Bio'
import { rhythm, scale } from '../utils/typography'
import { BlogTitle, DateText, PostText, BlogImgContainer, BlogImg, SocialMediaContainer } from './_ui'
import { BlogSection, Bg } from '../pages/_ui'
import { Banner } from '../pages/blog/_ui'
import {
  FacebookShareButton,
  GooglePlusShareButton,
  LinkedinShareButton,
  TwitterShareButton,
  TelegramShareButton,
  WhatsappShareButton,
  PinterestShareButton,
  VKShareButton,
  OKShareButton,
  RedditShareButton,
  TumblrShareButton,
  LivejournalShareButton,
  MailruShareButton,
  ViberShareButton,
  WorkplaceShareButton,
  EmailShareButton,
} from 'react-share'
import { Icon } from 'react-fa'
import { graphql, withPrefix } from 'gatsby'
import { DiscussionEmbed } from 'disqus-react'

class BlogPostTemplate extends React.Component {
  render() {
    let { location, data } = this.props
    const post = data.markdownRemark
    const siteTitle = get(this.props, 'data.site.siteMetadata.title')
    const { previous, next } = this.props.pageContext
    const imagePaths = get(post, 'frontmatter.images')
    const socialUrl = `https://www.evolutionofastudio.com${decodeURIComponent(post.fields.slug)}`
    const socialTitle = `Apartment inspo from Evolution Of A Studio: ${post.frontmatter.title}`

    const disqusShortname = 'evolutionofastudio';
    const disqusConfig = {
        url: socialUrl,
        identifier: post.fields.slug,
        title: post.frontmatter.title
    }

    // const socialUrl = `localhost:8000${decodeURIComponent(post.fields.slug)}`
    return (
      <Header {...this.props}>
        <Helmet title={`${post.frontmatter.title} | ${siteTitle}`}>
          <meta name="description" content={`${post.excerpt}... Practical and clever studio apartment layouts for daily family life`} />
        </Helmet>
        <Banner />
        <div style={{ position: 'relative', zIndex: 1 }}>
          <Bg />
          <BlogSection>

            <BlogTitle>{post.frontmatter.title}</BlogTitle>
            <DateText>
              {post.frontmatter.date}
            </DateText>
            <SocialMediaContainer>
              <FacebookShareButton
                url={socialUrl}
                hashtag="#evolutionofastudio"
              >
                <Icon name="facebook" />
              </FacebookShareButton>
              <TwitterShareButton
                url={socialUrl}
                title={socialTitle}
                hashtags={['evolutionofastudio', 'nyc', 'apartment']}
              >
                <Icon name="twitter" />
              </TwitterShareButton>
              <EmailShareButton
                url={socialUrl}
                subject={socialTitle}
                body={`${socialTitle}: ${socialUrl}`}
              >
                <Icon name="envelope" />
              </EmailShareButton>
              <GooglePlusShareButton
                url={socialUrl}
              >
                <Icon name="google-plus" />
              </GooglePlusShareButton>
              <RedditShareButton
                url={socialUrl}
                title={socialTitle}
              >
                <Icon name="reddit" />
              </RedditShareButton>
              {post.frontmatter.images && post.frontmatter.images[0] && <PinterestShareButton
                url={socialUrl}
                media={withPrefix(post.frontmatter.images[0].publicURL)}
                description={`${socialTitle}. ${post.excerpt}...`}
              >
                <Icon name="pinterest" />
              </PinterestShareButton>}
            </SocialMediaContainer>

            <PostText dangerouslySetInnerHTML={{ __html: post.html }} />
            <div style={{ display: 'flex', justifyContent: 'center' }}>
              {imagePaths && imagePaths.map(({ relativePath, publicURL }, i) => (
                <BlogImgContainer key={publicURL}>
                  <BlogImg key={relativePath} src={publicURL} />
                </BlogImgContainer>
              ))}
            </div>
            <hr
              style={{
                marginBottom: rhythm(1),
              }}
            />
            {/* <Bio /> */}

            <ul
              style={{
                display: 'flex',
                flexWrap: 'wrap',
                justifyContent: 'space-between',
                listStyle: 'none',
                padding: 0,
              }}
            >
              <li>
                {
                  previous &&
                  <Link to={previous.fields.slug} rel="prev">
                    ← {previous.frontmatter.title}
                  </Link>
                }
              </li>
              <li>
                {
                  next &&
                  <Link to={next.fields.slug} rel="next" style={{ textDecoration: 'none' }}>
                    {next.frontmatter.title} →
                  </Link>
                }
              </li>
            </ul>

            <DiscussionEmbed shortname={disqusShortname} config={disqusConfig} />
          </BlogSection>
        </div>


      </Header>
    )
  }
}

export default BlogPostTemplate

export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    site {
      siteMetadata {
        title
        author
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      html
      excerpt
      fields {
        slug
      }
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        image {
          relativePath
          publicURL
        }
        images {
          relativePath
          publicURL
        }
      }
    }
  }
`
